<template>
  <v-card>
    <v-card-title>
      <v-btn @click="onCancel" icon medium class="white--text mr-1">
        <v-icon medium dark> mdi-arrow-left </v-icon>
      </v-btn>
      <v-container fluild class="text-left">Rubix Upgrade</v-container>
    </v-card-title>
    <v-card-text>
      <v-container fluid>
        <v-row>
          <v-col md="6" lg="6">
            <v-card elecation="2" outlined :loading="cardLoading">
              <v-card-text>
                <v-row>
                  <v-col cols="8" sm="7" class="text-left">
                    <text-field-display value="Rubix Service"></text-field-display>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="3" class="text-left">
                    <text-field-display label="Version" :value="rubixAppStatus.version"></text-field-display>
                  </v-col>
                  <v-col cols="3" class="text-left">
                    <text-field-display
                      label="Deployment Mode"
                      :value="rubixAppStatus.deployment_mode && capitalize(rubixAppStatus.deployment_mode)"
                    ></text-field-display>
                  </v-col>
                  <v-col cols="6" class="text-right">
                    <rbx-button
                      v-if="showUpgradeToLatest"
                      :click="() => handleUpgradeClick('latest')"
                      title="Upgrade to Latest"
                      color="primary"
                    ></rbx-button>
                  </v-col>
                </v-row>
                <v-form v-model="valid" ref="upgradeForm" lazy-validation>
                  <v-row>
                    <v-col cols="12">
                      <v-combobox
                        :loading="isFetchingReleases"
                        :items="rubixAppReleases"
                        label="Select a release version"
                        hideSelected
                        clearable
                        v-model="selectedVersion"
                        item-text="name"
                        item-value="tag_name"
                        :rules="versionRules"
                      >
                      </v-combobox>
                    </v-col>
                    <v-col cols="12" class="text-right">
                      <rbx-button className="mr-2" :click="() => openUploadDialog()" title="Upload Build" color="#8cd0ab"></rbx-button>
                      <rbx-button
                        :click="() => handleUpgradeClick('version')"
                        title="Change Version"
                        color="pink"
                      ></rbx-button>
                    </v-col>
                  </v-row>
                </v-form>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-card-text>
    <confirm-dialog
      :is-open="upgradeDialog"
      :loading="isUpgrading"
      :on-close="() => closeDialog()"
      :on-confirm="() => handleDialogConfirm()"
      dialog-text="This action will logout your session! Do you want to upgrade or downgrade the app?"
    ></confirm-dialog>

    <v-dialog v-model="isUploadDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Upload & Upgrade</span>
        </v-card-title>

        <v-card-text>
          <v-alert outlined border="left" class="text-left" type="warning">
            Your session will be logged out after uploading and upgrading the build.
          </v-alert>
          <v-form v-model="buildUploadForm" ref="uploadUpgradeForm" lazy-validation>
            <v-row dense>
              <v-col cols="12">
                <v-file-input
                  v-model="file"
                  :rules="[requiredValidation]"
                  counter
                  show-size
                  @change="onFileSelect"
                  truncate-length="15"
                ></v-file-input>
              </v-col>
              <v-col cols="12" class="text-left pl-9">
                <v-text-field
                  label="Version"
                  type="text"
                  single-line
                  v-model="version"
                  :rules="[requiredValidation]"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeUploadDialog">Cancel</v-btn>
          <v-btn color="blue darken-1" :loading="isUploadingApp" text v-on:click="uploadApp">Upload & Upgrade</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>
<script>
import RbxButton from '../Common/RbxButton';
import ConfirmDialog from '../Common/ConfirmDialog';
import validation from '../../common/validation';
import TextFieldDisplay from '../Common/TextFieldDisplay';
import * as rubixService from '../../services/rubixService';
import { RUBIX_UPGRADE } from '../../constants/messageConstants';
import * as actionTypes from '../../store/actionTypes';
import LOCAL_STORAGE from '../../constants/localStorage.js';

export default {
  name: 'rubix-upgrade-view',
  props: ['on-cancel'],
  components: { TextFieldDisplay, RbxButton, ConfirmDialog },
  data() {
    return {
      valid: false,
      buildUploadForm: true,
      upgradeDialog: false,
      isUpgrading: false,
      rubixAppStatus: {},
      rubixAppReleases: [],
      selectedVersion: null,
      cardLoading: false,
      isFetchingReleases: false,
      currentAction: null,
      showUpgradeToLatest: false,
      isLatestVersion: false,
      isUploadDialog: false,
      versionRules: [validation.required],
      requiredValidation: validation.required,
      version: null,
      file: null,
      isUploadingApp: false,
    };
  },
  async mounted() {
    await this.fetchRubixStatus();
    await this.fetchReleases();
    this.showUpgradeButton();
  },
  computed: {
    upgradeMethodMap() {
      return {
        version: this.upgradeVersion,
        latest: this.upgradeLatest,
      };
    },
  },
  methods: {
    fetchRubixStatus() {
      this.cardLoading = true;
      return rubixService
        .fetchStatus()
        .then(data => {
          this.$store.commit(actionTypes.UPDATE_RUBIX_SERVICE_VERSION, data);
          this.rubixAppStatus = data;
          this.selectedVersion = this.rubixAppStatus.version;
        })
        .catch(err => {})
        .finally(() => {
          this.cardLoading = false;
        });
    },
    fetchReleases() {
      this.isFetchingReleases = true;
      return rubixService
        .fetchReleases()
        .then(data => {
          this.rubixAppReleases = data;
        })
        .catch(err => {})
        .finally(() => {
          this.isFetchingReleases = false;
        });
    },
    handleUpgradeClick(action) {
      if (action === 'version' && !this.$refs.upgradeForm.validate()) {
        return;
      }
      this.upgradeDialog = true;
      this.currentAction = action;
    },
    handleDialogConfirm() {
      this.upgradeMethodMap[this.currentAction]();
    },
    upgradeLatest() {
      this.isUpgrading = true;
      rubixService
        .upgradeLatest()
        .then(() => {
          this.$toasted.show(RUBIX_UPGRADE.success);
          this.logout();
        })
        .catch(err => {
          this.$toasted.show(RUBIX_UPGRADE.error, { type: 'error' });
        })
        .finally(() => {
          this.isUpgrading = false;
          this.closeDialog();
        });
    },
    upgradeVersion() {
      this.isUpgrading = true;
      rubixService
        .upgrade(this.selectedVersion)
        .then(() => {
          this.$toasted.show(RUBIX_UPGRADE.success);
          this.logout();
        })
        .catch(err => {
          this.$toasted.show(RUBIX_UPGRADE.error, { type: 'error' });
        })
        .finally(() => {
          this.isUpgrading = false;
          this.closeDialog();
        });
    },
    openUploadDialog() {
      this.isUploadDialog = true;
    },
    closeUploadDialog() {
      this.isUploadDialog = false;
    },
    capitalize(s) {
      if (typeof s !== 'string') return '';
      return s.charAt(0).toUpperCase() + s.slice(1);
    },
    closeDialog() {
      this.upgradeDialog = false;
    },
    showUpgradeButton() {
      this.showUpgradeToLatest =
        (Array.isArray(this.rubixAppReleases) &&
          this.rubixAppReleases.length > 0 &&
          this.rubixAppReleases[0] &&
          this.selectedVersion !== this.rubixAppReleases[0]) ||
        false;
    },
    uploadApp() {
      if (!this.$refs.uploadUpgradeForm.validate()) {
        return;
      }
      this.isUploadingApp = true;
      rubixService
        .upgradeViaFile(this.version, this.file)
        .then(() => {
          this.$toasted.show('Sucessfully uploaded and upgraded.');
          this.logout();
        })
        .catch(err => {})
        .finally(() => {
          this.isUploadingApp = false;
        });
    },
    logout() {
      localStorage.removeItem(LOCAL_STORAGE.JWT);
      localStorage.removeItem(LOCAL_STORAGE.TOKEN);
      localStorage.removeItem(LOCAL_STORAGE.USER);
      this.$router.push('/login');
    },
    onFileSelect() {
      if (this.file && this.file.name) {
        const fileName = this.file.name;
        const versionRegex = new RegExp('[0-9].[0-9].[0-9]');
        let versionNumber = fileName.match(versionRegex, '');
        if (versionNumber && Array.isArray(versionNumber)) {
          versionNumber = versionNumber[0];
          this.version = 'v' + versionNumber;
        }
      }
    },
  },
};
</script>
