<template>
  <full-screen-container :on-back="onCancel" title="Master/Slave Configuration">
    <v-row>
      <v-col md="6" sm="12">
        <v-card style="padding: 30px" :loading="forms.mqttRestBridge.isFetching">
          <v-card-title> MQTT Rest Bridge Configuration </v-card-title>
          <v-card-text>
            <v-form v-model="forms.mqttRestBridge.valid" ref="form" lazy-validation>
              <v-row dense>
                <v-col cols="6" lg="4" sm="6" md="6">
                  <v-switch
                    v-model="mqttRestBridge.master"
                    inset
                    class="pr-4"
                    label="Set as Master"
                    color="primary"
                  ></v-switch>
                </v-col>
                <v-col cols="6" sm="6" md="6">
                  <v-switch
                    v-model="mqttRestBridge.enabled"
                    inset
                    class="pr-4"
                    label="Enabled"
                    color="primary"
                  ></v-switch>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    label="Name"
                    v-model="mqttRestBridge.name"
                    required
                    :rules="[rules.required]"
                  ></v-text-field>
                </v-col>
                <v-col cols="8" sm="8" md="8">
                  <v-text-field
                    label="Host"
                    v-model="mqttRestBridge.host"
                    required
                    :rules="[rules.required]"
                  ></v-text-field>
                </v-col>
                <v-col cols="4" sm="4" md="4">
                  <v-text-field
                    label="Port"
                    type="number"
                    v-model="mqttRestBridge.port"
                    required
                    :rules="[rules.required]"
                  ></v-text-field>
                </v-col>

                <v-col cols="12" sm="12" md="12">
                  <v-switch
                    v-model="mqttRestBridge.authentication"
                    inset
                    class="pr-4"
                    label="Authenticate"
                    color="primary"
                  ></v-switch>
                </v-col>
                <v-col cols="12" sm="12" md="12" v-if="mqttRestBridge.authentication">
                  <v-text-field
                    label="UserName"
                    v-model="mqttRestBridge.username"
                    required
                    :rules="[rules.required]"
                  ></v-text-field>
                  <v-text-field
                    v-model="mqttRestBridge.password"
                    type="password"
                    label="Password"
                    required
                    :rules="[rules.required]"
                  ></v-text-field>
                </v-col>
                <v-col cols="6" sm="6" md="6">
                  <v-switch
                    v-model="mqttRestBridge.retain"
                    inset
                    class="pr-4"
                    label="Retain"
                    color="primary"
                  ></v-switch>
                </v-col>
                <v-col cols="6" sm="6" md="6">
                  <v-select v-model="mqttRestBridge.qos" :items="[0, 1, 2]" label="QoS"></v-select>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-switch
                    v-model="mqttRestBridge.attempt_reconnect_on_unavailable"
                    inset
                    class="pr-4"
                    label="Attempt Reconnect"
                    color="primary"
                  ></v-switch>
                </v-col>

                <v-col cols="6" sm="6" md="6" v-if="mqttRestBridge.attempt_reconnect_on_unavailable">
                  <v-text-field
                    v-model="mqttRestBridge.attempt_reconnect_secs"
                    type="number"
                    label="Attempt reconnect (in Seconds)"
                    required
                    :rules="[rules.required]"
                  ></v-text-field>
                </v-col>
                <v-col cols="6" sm="6" md="6" v-if="mqttRestBridge.attempt_reconnect_on_unavailable">
                  <v-text-field
                    v-model="mqttRestBridge.timeout"
                    type="number"
                    label="Reconnect Timeout (in Seconds)"
                    required
                    :rules="[rules.required]"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-card-text>
          <v-card-actions class="justify-end pa-4">
            <v-btn
              x-small
              class="btn-height-22 white--text"
              color="blue darken-2"
              :loading="forms.mqttRestBridge.isSaving"
              @click="
                () => {
                  confirmMRBConifg = true;
                }
              "
            >
              Save
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <confirm-dialog
      :is-open="confirmMRBConifg"
      :loading="forms.mqttRestBridge.isSaving"
      :on-close="
        () => {
          confirmMRBConifg = false;
        }
      "
      :on-confirm="() => saveMqttRestBridge()"
      dialog-text="This action will restart Rubix Service! Do you want to proceed?"
    ></confirm-dialog>
  </full-screen-container>
</template>
<script>
import _diff from 'lodash/difference';
import ConfirmDialog from '../Common/ConfirmDialog';
import FullScreenContainer from '../Common/FullScreenContainer';
import * as mqttRestBridgeService from '../../services/mqttRestBridgeService';

export default {
  name: 'master-slave-config',
  props: ['onCancel'],
  components: { ConfirmDialog, FullScreenContainer },
  data() {
    return {
      confirmMRBConifg: false,
      mqttRestBridge: {
        enabled: false,
        master: false,
        name: '',
        host: '',
        port: '',
        authentication: false,
        username: '',
        password: '',
        keepalive: 60,
        qos: 0,
        retain: false,
        attempt_reconnect_on_unavailable: false,
        attempt_reconnect_secs: 5,
        timeout: 5,
      },
      forms: {
        mqttRestBridge: {
          valid: false,
          isSaving: false,
          isDisabled: false,
          isFetching: false,
        },
      },
      rules: { required: value => !!value || 'Required.' },
    };
  },
  mounted() {
    this.fetchMqttRestBridge();
  },
  methods: {
    fetchMqttRestBridge() {
      this.forms.mqttRestBridge.isFetching = true;
      mqttRestBridgeService
        .fetchMRBListener()
        .then(data => {
          this.mqttRestBridge = data;
          this.$store.commit('changeMqttRestBridgeConfig', data);
        })
        .catch(err => {
          this.$toasted.show('Error fetching MQTT rest bridge config', { type: 'error' });
        })
        .finally(() => {
          this.forms.mqttRestBridge.isFetching = false;
        });
    },
    saveMqttRestBridge() {
      this.forms.mqttRestBridge.isSaving = true;
      mqttRestBridgeService
        .pathMRBListener(this.mqttRestBridge)
        .then(data => {
          this.$toasted.show('Successfully updated MQTT rest bridge config');
          this.confirmMRBConifg = false;
          this.$store.commit('changeMqttRestBridgeConfig', { ...data } || {});
          if (this.$router.currentRoute.path == '/master-slaves' && !(data && data.master)) {
            this.$router.push({ path: 'services' });
          }
        })
        .catch(err => {
          this.$toasted.show('Error updating MQTT rest bridge config', { type: 'error' });
        })
        .finally(() => {
          this.forms.mqttRestBridge.isSaving = false;
        });
    },
  },
};
</script>
