<template>
  <v-card>
    <v-card-title>
      <v-btn v-if="hasBackButton" @click="onCancel" icon medium class="white--text mr-1">
        <v-icon medium dark> mdi-arrow-left </v-icon>
      </v-btn>
      <v-container fluild class="text-left">Update Password</v-container>
    </v-card-title>
    <v-container fluild>
      <v-row>
        <v-col md="4" sm="12">
          <v-card-text>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col sm="12">
                  <v-text-field
                    v-model="currentPassword"
                    type="password"
                    @input="onChangePassword"
                    label="Current Password"
                    required
                    :rules="[rules.required]"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col sm="12">
                  <v-text-field
                    v-model="newPassword"
                    type="password"
                    @input="onChangePassword"
                    label="New Password"
                    required
                    :rules="newPasswordRules"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col sm="12">
                  <v-text-field
                    v-model="retypePassword"
                    type="password"
                    @input="onChangePassword"
                    label="Retype Password"
                    required
                    :rules="[rules.required]"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-card-text>
          <v-spacer></v-spacer>
          <v-card-actions class="align-end justify-end">
            <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
            <v-btn color="blue darken-1" :disabled="disable" text @click="onSubmit(formData)"> Save </v-btn>
          </v-card-actions>
        </v-col>
      </v-row>
    </v-container>
  </v-card>
</template>
<script>
import * as localStorageUtil from '../../utils/localstorage';
import * as passwordService from '../../services/passwordService';

export default {
  props: ['onSave', 'onCancel', 'hasBackButton'],
  data: () => ({
    rules: { required: value => !!value || 'Required' },
    newPasswordRules: [
      value => !!value || 'Required.',
      value => !!value.match('(?=.*?[A-Z])') || 'At least 1 Uppercase',
      value => !!value.match('(?=.*?[0-9])') || 'At least 1 Number',
      value => value.length >= 8 || 'At least 8 charaters',
    ],
    valid: false,
    newPassword: '',
    retypePassword: '',
    currentPassword: '',
    disable: true,
  }),
  methods: {
    onChangePassword() {
      this.disable = true;
      if (this.newPassword.match('(?=.*?[A-Z])(?=.*?[0-9]).{8,}') && this.retypePassword === this.newPassword) {
        this.disable = false;
      }
    },
    logout() {
      localStorageUtil.removeItem('jwt');
      localStorageUtil.removeItem('user');
      this.$router.push('/login');
    },
    async onSubmit(data) {
      if (data.newPassword.match('(?=.*?[A-Z])(?=.*?[0-9]){8,}')) {
        passwordService.updatePassword(data).then(res => {
          this.$toasted.show('Changed Password Successfully', { type: 'success' });
          typeof this.onSave === 'function' && this.onSave();
          this.logout();
        });
      }
    },
  },
  computed: {
    formData() {
      const data = {
        currentPassword: this.currentPassword,
        newPassword: this.newPassword,
        retypePassword: this.retypePassword,
      };
      return data;
    },
  },
};
</script>
<style scoped>
.noti-regex {
  margin-left: 10px;
}
</style>
