<template>
  <full-screen-container :on-back="onCancel" title="Update Token">
    <v-row>
      <v-col md="6" sm="12">
        <v-card :loading="isUpdating" outlined>
          <v-card-title>Token Update</v-card-title>
          <v-card-text>
            <p class="mb-0 text-left" v-if="tokenHint">
              <span class="caption">Previous Token:</span>
              <span class="white--text body-1">{{ tokenHint }}</span>
            </p>
            <v-form v-model="valid" ref="tokenUpdateForm" lazy-validation>
              <v-text-field label="Third party token" v-model="token" :rules="tokenRules"></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions class="justify-end pa-4">
            <v-btn x-small class="mr-1 btn-height-22 white--text" color="blue darken-2" @click="onSave">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </full-screen-container>
</template>
<script>
import FullScreenContainer from '../Common/FullScreenContainer';
import validation from '../../common/validation';
import * as rubxixService from '../../services/rubixService';
export default {
  name: 'token-update',
  components: { FullScreenContainer },
  props: ['on-cancel'],
  data() {
    return {
      valid: true,
      token: '',
      tokenHint: '',
      isUpdating: false,
      tokenRules: [validation.required],
    };
  },
  created() {
    this.getHintToken();
  },
  methods: {
    getHintToken() {
      rubxixService.fetchTokenHint().then(data => {
        this.tokenHint = data.token;
      });
    },
    onSave() {
      if (!this.$refs.tokenUpdateForm.validate()) {
        return;
      }
      this.isUpdating = true;
      rubxixService
        .updateToken(this.token)
        .then(() => {
          this.$toasted.show('Sucessfully updated token');
          this.onCancel();
        })
        .catch(err => {
          this.$toasted.show('Sucessfully updated token', { type: 'error' });
        })
        .finally(() => {
          this.isUpdating = false;
        });
    },
  },
};
</script>
