<template>
  <div>
    <v-list-item>
      <v-menu top :offset-y="true">
        <template v-slot:activator="{ on, attrs }">
          <v-list-item-icon>
            <v-icon v-on="on" v-bind="attrs">mdi-dots-vertical-circle-outline</v-icon>
          </v-list-item-icon>
        </template>
        <v-card class="mx-auto" max-width="300" tile>
          <v-list dense>
            <v-list-item>
              <v-list-item-avatar size="24">
                <v-avatar size="24" height="24" width="24" color="indigo">
                  <v-icon size="24" dark> mdi-account-circle </v-icon>
                </v-avatar>
              </v-list-item-avatar>

              <v-list-item-content>
                <v-list-item-title class="text-left justify-space-between">
                  Admin &nbsp;|&nbsp;
                  <span class="caption">{{ appVersion.version }}</span>
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-list-item
              :key="index"
              v-for="(menuItem, index) in menuItems"
              @click="handleActionClick(menuItem.actionName, menuItem)"
            >
              <v-list-item-avatar size="24">
                <v-icon>{{ menuItem.icon }}</v-icon>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title class="text-left">
                  <span>{{ menuItem.displayName }}</span>
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-card>
      </v-menu>
    </v-list-item>
    <v-dialog fullscreen v-model="dialog.passwordDialog">
      <update-password-form
        :hasBackButton="true"
        :onCancel="() => closeDialog(dialogNames.passwordDialog)"
      ></update-password-form>
    </v-dialog>
    <v-dialog fullscreen v-model="dialog.configureEndpointDialog">
      <configure-endpoint-view
        v-if="dialog.configureEndpointDialog"
        :onCancel="() => closeDialog(dialogNames.configureEndpointDialog)"
      ></configure-endpoint-view>
    </v-dialog>
    <v-dialog fullscreen v-model="dialog.rubixUpgradeDialog">
      <rubix-upgrade-view
        v-if="dialog.rubixUpgradeDialog"
        :onCancel="() => closeDialog(dialogNames.rubixUpgradeDialog)"
      ></rubix-upgrade-view>
    </v-dialog>
    <v-dialog fullscreen v-model="dialog.tokenUpdateDialog">
      <token-update
        v-if="dialog.tokenUpdateDialog"
        :onCancel="() => closeDialog(dialogNames.tokenUpdateDialog)"
      ></token-update>
    </v-dialog>
    <v-dialog fullscreen v-model="dialog.deviceInfoDialog">
      <device-info
        v-if="dialog.deviceInfoDialog"
        :onCancel="() => closeDialog(dialogNames.deviceInfoDialog)"
      ></device-info>
    </v-dialog>

    <v-dialog fullscreen v-model="dialog.masterSlaveConfigDialog">
      <master-slave-config
        v-if="dialog.masterSlaveConfigDialog"
        :onCancel="() => closeDialog(dialogNames.masterSlaveConfigDialog)"
      ></master-slave-config>
    </v-dialog>
  </div>
</template>
<script>
import DeviceInfo from './DeviceInfo';
import TokenUpdate from './TokenUpdate';
import RubixUpgradeView from './RubixUpgradeView.vue';
import UpdatePasswordForm from './UpdatePasswordForm';
import ConfigureEndpointView from './ConfigureEndpointView';
import * as localStorageUtil from '../../utils/localstorage';
import LOCAL_STORAGE_CONSTANT from '../../constants/localStorage';
import MasterSlaveConfig from './MasterSlaveConfig.vue';

export default {
  name: 'nav-bar-three-dot-settings',
  components: {
    DeviceInfo,
    TokenUpdate,
    UpdatePasswordForm,
    ConfigureEndpointView,
    RubixUpgradeView,
    MasterSlaveConfig,
  },
  data() {
    const dialogs = {
      passwordDialog: 'passwordDialog',
      configureEndpointDialog: 'configureEndpointDialog',
      deviceInfoDialog: 'deviceInfoDialog',
      rubixUpgradeDialog: 'rubixUpgradeDialog',
      tokenUpdateDialog: 'tokenUpdateDialog',
      masterSlaveConfigDialog: 'masterSlaveConfigDialog',
    };
    const buttonActions = {
      updatePassword: '@action/updatePassword',
      upgradeRubix: '@action/upgradeRubix',
      logout: '@action/logout',
      tokenUpdate: '@action/tokenUpdate',
      configureEndpoint: '@action/configureEndpoint',
      deviceInfo: '@action/deviceInfo',
      masterSlaveConfig: '@action/masterSlaveConfig',
    };
    const menuItemConfig = {
      rubixUpgradeConfig: {
        name: 'rubixUpgradeConfig',
        displayName: 'Upgrade Rubix',
        hasDialog: true,
        dialogName: dialogs.rubixUpgradeDialog,
        actionName: buttonActions.upgradeRubix,
        icon: 'mdi-arrow-up',
      },
      deviceInfo: {
        name: 'deviceInfo',
        displayName: 'Device Info',
        hasDialog: true,
        dialogName: dialogs.deviceInfoDialog,
        actionName: buttonActions.deviceInfo,
        icon: 'mdi-cellphone-settings',
      },
      masterSlaveConfig: {
        name: 'masterSlaveConfig',
        displayName: 'Master/Slave Configuration',
        hasDialog: true,
        dialogName: dialogs.masterSlaveConfigDialog,
        actionName: buttonActions.masterSlaveConfig,
        icon: 'mdi-account-child-outline',
      },
      tokenUpdate: {
        name: 'tokenUpdate',
        displayName: 'Token update',
        hasDialog: true,
        dialogName: dialogs.tokenUpdateDialog,
        actionName: buttonActions.tokenUpdate,
        icon: 'mdi-update',
      },
      endpointConfig: {
        name: 'endpointConfig',
        displayName: 'Configure Endpoints',
        hasDialog: true,
        dialogName: dialogs.configureEndpointDialog,
        actionName: buttonActions.configureEndpoint,
        icon: 'mdi-link',
      },
      updatePassword: {
        name: 'updatePassword',
        displayName: 'Update Password',
        hasDialog: true,
        dialogName: dialogs.passwordDialog,
        actionName: buttonActions.updatePassword,
        icon: 'mdi-textbox-password',
      },
      logout: {
        name: 'logout',
        displayName: 'Logout',
        hasDialog: false,
        dialogNames: null,
        actionName: buttonActions.logout,
        icon: 'mdi-logout-variant',
      },
    };
    return {
      passwordDialog: false,
      dialogNames: { ...dialogs },
      buttonActions: { ...buttonActions },
      menuItems: Object.values(menuItemConfig),
      dialog: {
        [dialogs.rubixUpgradeDialog]: false,
        [dialogs.passwordDialog]: false,
        [dialogs.configureEndpointDialog]: false,
        [dialogs.tokenUpdateDialog]: false,
        [dialogs.deviceInfoDialog]: false,
        [dialogs.masterSlaveConfigDialog]: false,
      },
    };
  },
  computed: {
    userName() {
      return localStorageUtil.getItem(LOCAL_STORAGE_CONSTANT.USER);
    },
    appVersion() {
      return this.$store.state.rubixAppVersion;
    },
    setting() {
      return this.$store.state.setting;
    },
  },
  methods: {
    handleActionClick(action, buttonConfig) {
      if (buttonConfig.hasDialog && buttonConfig.dialogName) {
        this.openDialog(buttonConfig.dialogName);
      }

      switch (action) {
        case this.buttonActions.logout:
          this.logout();
          break;
        case this.buttonActions.updatePassword:
          break;
      }
    },
    logout() {
      localStorageUtil.removeItem('jwt');
      localStorageUtil.removeItem('user');
      this.$router.push('/login');
    },
    openDialog(dialogName) {
      this.dialog[dialogName] = true;
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
    },
  },
};
</script>
