<template>
  <v-navigation-drawer
    permanent
    v-model="drawer"
    :mini-variant.sync="mini"
    class="float-left side-bar"
    id="drawer-menu"
  >
    <div>
      <v-list-item class="px-3 pt-3 pb-3">
        <v-list-item-avatar style="padding: 3px">
          <v-img :src="logo"></v-img>
        </v-list-item-avatar>
        <v-list-item-title class="text-left">
          <span>Rubix Platform</span>
          <p class="text-center text-xs caption mb-n2 orange--text" v-if="isSwitchedSlave">
            Slave Mode Active
            <tooltip-icon-button
              :onClick="exitSlaveMode"
              btnIcon="mdi-location-exit"
              btnTooltip="Exit slave mode"
              btn-color="orange"
            ></tooltip-icon-button>
          </p>
          <div class="switch-device-title" v-if="deviceInfo">
            <v-tooltip bottom color="black">
              <template v-slot:activator="{ on, attrs }">
                <span class="client-name" v-bind="attrs" v-on="on"> {{ deviceInfo.client_name }}</span>
              </template>
              <span>
                <sidebar-device-info
                  v-if="deviceInfo"
                  :clientName="deviceInfo.client_name"
                  :siteName="deviceInfo.site_name"
                  :deviceName="deviceInfo.device_name"
                  title="Current Device"
                >
                </sidebar-device-info>
                <sidebar-device-info
                  v-if="isSwitchedSlave && switchSlaveDeviceInfo"
                  :clientName="switchSlaveDeviceInfo.client_name"
                  :siteName="switchSlaveDeviceInfo.site_name"
                  :deviceName="switchSlaveDeviceInfo.device_name"
                  title="Switched Device"
                >
                </sidebar-device-info>
              </span>
            </v-tooltip>
          </div>
        </v-list-item-title>
        <v-btn icon @click.stop="minimizeMenu">
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>
      </v-list-item>
    </div>

    <div class="text-left pl-4 lock-icon-button">
      <tooltip-icon-button
        :btnColor="setting.lockSidebar ? 'blue' : 'white'"
        buttonSize="small"
        btnIcon="mdi-lock"
        btnTooltip="Lock/Unlock Sidebar"
        :onClick="() => changeLock()"
      ></tooltip-icon-button>
    </div>
    <v-divider></v-divider>
    <vertical-height-container :vh="sidebarContainerHeight">
      <template v-slot:children>
        <div class="list-box-2">
          <v-list dense nav class="mt-3">
            <div v-for="item in items" :key="item.key" style="text-align: start">
              <!-- no child -->
              <v-list-item v-if="!item.children" link :to="item.link">
                <v-list-item-icon>
                  <v-tooltip right>
                    <template v-slot:activator="{ on, attrs }">
                      <v-icon v-bind="attrs" v-on="on"> {{ item.icon }} </v-icon>
                    </template>
                    <span>{{ item.title }}</span>
                  </v-tooltip>
                </v-list-item-icon>
                <v-list-item-content>
                  <v-list-item-title>{{ item.title }}</v-list-item-title>
                </v-list-item-content>
              </v-list-item>
              <!-- have childs -->
              <v-list-group
                class="parent-bar"
                v-if="item.children && item.children.length > 0"
                :key="item.key"
                no-action
                :prepend-icon="item.icon"
              >
                <template v-slot:activator>
                  <v-list-item-content>
                    <v-list-item-title v-text="item.title"></v-list-item-title>
                  </v-list-item-content>
                </template>
                <v-list-item v-for="sub in item.children" :key="sub.title" link :to="sub.link" style="color: white">
                  <v-list-item-icon class="sub-service-icon">
                    <v-icon>{{ sub.icon }}</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title>{{ sub.title }}</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list-group>
            </div>
            <v-list-item v-bind:key="iframe._id" v-for="iframe in iframes" link>
              <v-list-item-icon @click="onClickIframeItem(iframe)">
                <v-tooltip right>
                  <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on"> {{ `mdi-${iframe.icon}` }} </v-icon>
                  </template>
                  <span>{{ iframe.name }}</span>
                </v-tooltip>
              </v-list-item-icon>
              <v-list-item-content @click="onClickIframeItem(iframe)">
                <v-list-item-title style="text-align: start">{{ iframe.name }}</v-list-item-title>
              </v-list-item-content>
              <v-list-item-action v-if="iframe.type !== 'iframeOnly'">
                <v-icon @click="clickOnLinkIcon(iframe)" small color="grey lighten-1">mdi-open-in-new</v-icon>
              </v-list-item-action>
            </v-list-item>
            <v-list-item link :to="'/setting'">
              <v-list-item-icon>
                <v-tooltip right>
                  <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on"> mdi-settings </v-icon>
                  </template>
                  <span>Settings</span>
                </v-tooltip>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title style="text-align: start">Settings</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </div>
      </template>
    </vertical-height-container>

    <div class="list-box-3">
      <div>
        <div v-if="!mini">
          <div class="mini-device-info" v-if="deviceInfo">
            <p class="overline">Current Device</p>
            <p><span class="info-title">Site Name: </span>{{ deviceInfo.site_name }}</p>
            <p><span class="info-title">Device Name: </span>{{ deviceInfo.device_name }}</p>
          </div>
          <div class="mini-device-info" v-if="isSwitchedSlave && switchSlaveDeviceInfo">
            <p class="overline">Switched Device</p>
            <p><span class="info-title">Site Name: </span>{{ switchSlaveDeviceInfo.site_name }}</p>
            <p><span class="info-title">Device Name: </span>{{ switchSlaveDeviceInfo.device_name }}</p>
          </div>
        </div>
        <v-list-item v-if="mini">
          <v-tooltip bottom color="black">
            <template v-slot:activator="{ on, attrs }">
              <v-icon v-bind="attrs" v-on="on" medium dark>mdi-information-outline</v-icon>
            </template>
            <span>
              <sidebar-device-info
                v-if="deviceInfo"
                :clientName="deviceInfo.client_name"
                :siteName="deviceInfo.site_name"
                :deviceName="deviceInfo.device_name"
                title="Current Device"
              >
              </sidebar-device-info>
              <sidebar-device-info
                v-if="isSwitchedSlave && switchSlaveDeviceInfo"
                :clientName="switchSlaveDeviceInfo.client_name"
                :siteName="switchSlaveDeviceInfo.site_name"
                :deviceName="switchSlaveDeviceInfo.device_name"
                title="Switched Device"
              >
              </sidebar-device-info>
            </span>
          </v-tooltip>
        </v-list-item>
      </div>
      <nav-bar-three-dot-settings> </nav-bar-three-dot-settings>
    </div>
  </v-navigation-drawer>
</template>

<script>
import _cloneDeep from 'lodash/cloneDeep';
import logo from '../../assets/nube-frog-green.png';
import { sidebarItems } from '../../constants/appConstant';
import UpdatePasswordForm from './UpdatePasswordForm';
import SidebarDeviceInfo from './SidebarDeviceInfo.vue';
import NavBarThreeDotSettings from './NavBarThreeDotSettings';
import * as rubixService from '../../services/rubixService';
import * as settingService from '../../services/settingService';
import * as slaveAppsUiService from '../../services/slaveAppsUiService';
import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';
import VerticalHeightContainer from '../Common/VerticalHeightContainer.vue';

export default {
  components: {
    UpdatePasswordForm,
    NavBarThreeDotSettings,
    SidebarDeviceInfo,
    TooltipIconButton,
    VerticalHeightContainer,
  },
  props: ['drawer'],
  data: () => ({
    mini: true,
    switchSlaveUUID: '',
    switchSlaveDeviceInfo: null,
    logo,
    sidebarItems,
    password: {},
    passData: '',
    dialog: false,
    css: '',
    test: false,
    deviceInfo: {},
  }),
  computed: {
    iframes() {
      const { iframes } = this.$store.state;
      return iframes.filter(i => i.show);
    },
    isSwitchedSlave() {
      return this.$store.state.isSwitchedSlave;
    },
    items() {
      const { setting, mqttRestBridgeConfig } = this.$store.state;
      const menuItems = _cloneDeep(sidebarItems);
      const result = [];
      for (const item of menuItems) {
        if (setting[item.key]) {
          result.push(item);
        }
        if (item.key == 'masterSlaves' && mqttRestBridgeConfig && mqttRestBridgeConfig.master) {
          result.push(item);
        }
      }
      return result;
    },
    setting() {
      return this.$store.state.setting;
    },
    showSidebarContent() {
      return this.mini && this.deviceInfo;
    },
    sidebarContainerHeight() {
      if (this.mini) {
        return 80;
      }
      if (!this.deviceInfo && !this.isSwitchedSlave) {
        return 80;
      }
      if (this.deviceInfo && !this.isSwitchedSlave) {
        return 79;
      }
      return 73;
    },
  },
  watch: {
    isSwitchedSlave(newVal, oldVal) {
      const selectedDeviceInfo = slaveAppsUiService.getAppSlaveDeviceInfo();
      if (selectedDeviceInfo) {
        this.switchSlaveDeviceInfo = selectedDeviceInfo;
      }
    },
  },
  mounted() {
    this.mini = !this.setting.lockSidebar;
    this.getDeviceInfo();
  },
  updated() {
    this.css =
      !this.setting.lockSidebar && this.mini
        ? 'position:absolute;bottom:50px'
        : 'position:absolute;bottom:13px;right:10px';
  },
  methods: {
    onClickIframeItem({ ipType, port, type, ip, _id }) {
      let url = `http://${ip}:${port}`;
      if (ipType === 'userBrowserIp') {
        url = `http://${location.hostname}:${port}`;
      }
      if (type === 'linkOnly') {
        window.open(url, '_blank');
        return;
      }
      this.$router.replace(`/iframe/${_id}`);
    },
    clickOnLinkIcon({ ip, port, ipType }) {
      let url = 'http://' + ip + ':' + port;
      if (ipType === 'userBrowserIp') {
        url = `http://${location.hostname}:${port}`;
      }
      window.open(url, '_blank');
    },
    onCancel() {
      this.dialog = false;
      this.test = false;
    },
    changeLock() {
      this.setting.lockSidebar = !this.setting.lockSidebar;
      settingService.update(this.setting).then(result => {
        if (result === 'successful') {
          this.$store.commit('changeSetting', this.setting);

          this.$toasted.show('Success!');
        }
      });
    },
    minimizeMenu() {
      if (!this.setting.lockSidebar) {
        this.mini = !this.mini;
      }
    },
    getDeviceInfo() {
      rubixService.fetchPlatInfo().then(data => {
        this.deviceInfo = data;
      });
    },
    exitSlaveMode() {
      slaveAppsUiService.removeAppSlaveId();
      window.location.reload();
      this.$store.commit('changeAppRerenderCount');
    },
  },
};
</script>
